@import "../variables.scss";
@import "../mixins.scss";


* {
    margin : 0;
    padding :  0;
    box-sizing : border-box;
    font-family: "PT Sans",sans-serif;
}

#content  {
    line-height: 1.3;
    min-height: 60vh;
    display: flex;
    flex-wrap : wrap;
    @include respond("mobile") {
        flex-direction: column;
    }


    .action-icon {
        margin-left : .5rem;
    }

    .item-link {
        cursor: pointer;
    }

    .container-left  {
        position: relative;
        flex : 0 0 100%;
        display: flex;
        flex-direction: column;
        flex-wrap : wrap;
        padding : 2rem;
        background-color: #303030;
        color : white;
        margin: 1.5rem 0rem 2rem;
    }

    .main-title {
        position: relative;
        margin-top : 2rem;
        flex :  0 0 100%;
        text-align: center;
        font-size : 2rem;
    }

    .main-title::after {
        content : "";
        margin: auto;
        margin-top : .5rem;
        display: block;
        width: 14rem;
        background-color : $base-color-pink;
        height : 4px;
    }


    ul {
        list-style-type: none;

        li {
            color : white;

            button {
                color : inherit;
            }
        }
    }

    .main-profile {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items : center;
        text-align: center;
        color : white;
        font-weight: 600;
        font-size : 1rem;

        .greet-user {
            font-size : 1.5rem;
            flex :  0 0  100%;
            text-align: left;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
        }

        .email {
            padding : .4rem .9rem;
            background-color: $base-color-pink;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
        }

        .age {
            padding : .4rem .9rem;
            background-color: $base-color-pink;
            margin-top : 1rem;
            margin-right : 1rem;
            border-radius: 10px;
        }

        .balance {
            margin-top : 1rem;
            padding : .4rem .9rem;
            border-radius: 10px;
            background-color: $base-color-pink;
        }

        .favorite-tag-list {
            flex : 0 0 100%;
            margin-top: 1.5rem;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            justify-content: flex-start;

            li {
                font-size : 1rem;
                margin-top : 1rem;
                margin-right : 1rem;
                align-self: flex-end;
                padding : .4rem .9rem;
                border-radius: 10px;
                background-color : #d43651;
            }

            .favorite-tags {
                margin-bottom: .5rem;
                font-size : 1.2rem;
                padding : 0;
                font-weight: 600;
                width: 100%;
                background-color: transparent;
                text-align: left;
            } 

            .edit-tags {
                background-color: #278acc;
            }
        }
    }

    .action-profile {
        @include respond("mobile") {
            position: relative;
            margin-top : 3rem;
            flex-direction: row;
            padding : 0;
        }
        position: absolute;
        right : 0rem;
        top : 1rem;
        padding : 2rem;
        display: flex;
        flex-wrap : wrap;
        flex-direction: column-reverse;



        .btn-action {
            margin-right: .5rem;
            display : block;
            margin-bottom : 1.5rem;
            font-weight: 600;
            font-size : 1rem;
            padding : .4rem .9rem;
            border : none;
            outline : none;
            background-color : #25a14e;
            border-radius: 10px;
        }
    }
}